@import url("https://fonts.googleapis.com/css2?family=Philosopher&display=swap");

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Philosopher", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(229, 238, 249);

  /* min-height: 100vh;
    background: no-repeat url("../public/bg1.jpeg");
  background-size: cover; */
}

img, video {
  border-radius: 10px;
  border: thick double #479bf1;
}

a {
  color: inherit;
  text-decoration: none;
}

p {
  text-indent: 25px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

iframe {
  border: 0;
}

h1,
h2,
h3 {
  text-align: center;
}

.container {
  width: 80%;
  margin: auto;
}

:target::before {
  content: "";
  display: block;
  height: 60px; /* Высота вашего фиксированного header */
  margin: -60px 0 0; /* Отрицательный margin равный высоте header */
}
